<template>
    <div>
        <h3></h3>
        <div class="box">
            <div class="item">
                    <p></p>
                    <div class="content">
                       <el-upload
                        class="upload-demo"
                        action="/api/upload/"
                        :on-success="handleSuccess"
                        :on-remove="handleRemove"
                        :file-list="fileList"
                        list-type="picture"
                        :before-upload='limitImage'
                        :limit='1'>
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/jpeg/png100kb</div>
                    </el-upload>
                    </div>
                
            </div>
        
           

            <div class="item">
                <p></p>
                <div class="content">
                    <el-input type="text" v-model="title" placeholder=""></el-input>
                </div>
            </div>   

            
            <div class="item">
                <el-button type="success" class="pull-right" @click="submit"></el-button>
            </div>

        </div>
    </div>
    
</template>

<script>

import { photo_select_id,delete_pic,photo_edit } from 'api/photo';
import { Message } from 'element-ui'

export default {
    async mounted(){
        //id
        this.pic_id = this.$route.query.id*1;
        let res = await photo_select_id({id:this.pic_id});
        
        //
        if(res.pic_url)  this.fileList.splice(0,1,{url:res.pic_url});  //
        this.title = res.pic_title;
        this.url = res.pic_url;
        this.pic_name = res.pic_name;
   
  },
  data() {
    return {
      fileList: [], //
      url:"", //
      title:"",
      pic_name:"", //
      pic_id:-1,//id
    };
  },
  methods: {
    limitImage(file){
        let type = file.type;
        // 100k
        if(file.size/1024>100){
            Message.error({
                    message: ""
            })
                return false
        }
        if(type != ' image/jpeg ' ||  type != 'image/jpg' || type != 'image/png' ){
            Message.error({
                    message: ""
            })
            return false
        }
    },  
    handleSuccess(data) {
        this.url = data;
        this.pic_name = data.replace('/api/img/','');
    },
    handleRemove() {
        //
        delete_pic({pic_name:this.pic_name});
        this.url = "";
        this.pic_name = "";
    },
    submit(){
        photo_edit({
            id:this.pic_id,
            url:this.url,
            title:this.title,
            pic_name:this.pic_name
        })
    }
  }
};
</script>

<style lang="less" scoped>
.box {
  width: 500px;
  margin: 0 auto;
  .item {
    padding-left: 50px;
    margin-bottom: 20px;

    .content {
      padding-left: 60px;
    }
  }
}
</style>



